:root {
  --color-white: #ffffff;
  --color-primary: #ffffff;
  --color-primary-text: #494c6b;
  --color-secondary-text: #9394a5;
  --color-light-text: #bbbbbb;
  --color-active: #3a7cfd;
  --color-circle: #d6d6d6;
  --color-background: #fafafa;
  --color-background-hover: #ebebeb;
  --color-background-modal: rgba(73, 76, 107, 0.6);
  --color-error: #cd1237;
}

.dark {
  --color-primary: #25273c;
  --color-primary-text: #e4e5f1;
  --color-secondary-text: #cacde8;
  --color-light-text: rgba(212, 212, 212, 0.4);
  --color-circle: rgba(182, 182, 182, 0.3);
  --color-background: #161722;
  --color-background-hover: #1c1e35;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  font-family: "Josefin Sans", sans-serif;
  background-color: var(--color-background);
  transition: 0.3s;
  line-height: 1.2;
}

ul {
  list-style: none;
}

h1.main-title {
  color: var(--color-white);
  text-transform: uppercase;
  letter-spacing: 14px;
}
@media screen and (min-width: 600px) {
  h1.main-title {
    font-size: 2.8rem;
  }
}

input:focus {
  outline-color: var(--color-active);
}

input:placeholder-shown {
  color: var(--color-secondary-text);
}

.toggle-theme {
  cursor: pointer;
  transition: 0.3s;
}
.toggle-theme:hover {
  filter: brightness(1) sepia(1) saturate(10000%);
}

.todo__list--item {
  display: flex;
  justify-content: space-between;
  gap: 1.35rem;
  padding: 1.25rem 1rem;
  color: var(--color-primary-text);
  cursor: move;
  position: relative;
}
@media screen and (min-width: 600px) {
  .todo__list--item {
    padding: 1.5rem;
  }
}
@media screen and (min-width: 600px) and (hover: hover) {
  .todo__list--item:hover .delete-icon {
    visibility: visible;
    opacity: 1;
  }
}
.todo__list--item:not(l:last-of-type) {
  border-bottom: 1px solid var(--color-light-text);
}
.todo__list--item span {
  position: absolute;
  top: calc(1.25rem - 1px);
  left: calc(1rem - 1px);
  border-radius: 50%;
  display: block;
  width: 26px;
  height: 26px;
  background-image: linear-gradient(135deg, #57ddff, #c058f3);
  opacity: 0;
  transition: 0.5s;
}
@media screen and (min-width: 600px) {
  .todo__list--item span {
    top: calc(1.5rem - 1px);
    left: calc(1.5rem - 1px);
  }
}
.todo__list--item .circle:hover {
  border: 0px solid var(--color-circle);
}
.todo__list--item .circle:hover + span {
  opacity: 1;
}
.todo__list--item p {
  margin-right: auto;
  align-self: center;
  font-size: 1.125rem;
}
.todo__list--item .checked-icon {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.2s, visibility 0.2s;
}
.todo__list--item .circle {
  cursor: pointer;
}
.todo__list--item .circle[data-checkable=true] {
  background: linear-gradient(135deg, #57ddff, #c058f3) no-repeat center/cover;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
.todo__list--item .circle[data-checkable=true] + p {
  text-decoration: line-through;
  color: var(--color-light-text);
}

.todo__list--item.checked .checked-icon {
  opacity: 1;
  visibility: visible;
  pointer-events: none;
}

.delete-icon {
  width: 20px;
  height: 20px;
  cursor: pointer;
}
@media screen and (min-width: 600px) and (hover: hover) {
  .delete-icon {
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.3s, visibility 0.3s;
  }
  .delete-icon:hover {
    visibility: visible;
    opacity: 1;
  }
}
@media (hover: none) {
  .delete-icon {
    visibility: visible;
    opacity: 1;
  }
}
.delete-icon svg path {
  transition: fill 0.4s;
}
.delete-icon:hover svg path {
  fill: var(--color-error);
}

.modal__delete.show {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.modal__delete.hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal__delete {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: var(--color-background-modal);
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  transition: 0.4s;
  /* GLASSMORPHISM*/
  -webkit-backdrop-filter: blur(15.5px);
  backdrop-filter: blur(12.5px);
}
.modal__delete .modal__delete--content {
  width: min(90%, 400px);
  background-color: var(--color-primary);
  color: var(--color-primary-text);
  border-radius: 5px;
  padding: 2rem;
}
.modal__delete .modal__delete--content p {
  margin-bottom: 1.5rem;
}

.btn-primary {
  padding: 0.5rem 1rem;
  border: 0;
  font-family: inherit;
  border-radius: 5px;
  background-color: var(--color-background);
  color: var(--color-primary-text);
  cursor: pointer;
  transition: 0.4s;
}
.btn-primary:hover {
  background-color: var(--color-background-hover);
}
.btn-primary.accept {
  color: var(--color-white);
  background-color: var(--color-error);
}
.btn-primary.accept:hover {
  opacity: 0.8;
}

.dragging {
  background-color: var(--color-primary-text);
  color: var(--color-primary);
  opacity: 0.7;
  transition: 0.4s;
}

.circle {
  width: 24px;
  min-width: 24px;
  height: 24px;
  min-height: 24px;
  border-radius: 50%;
  border: 1px solid var(--color-circle);
  background-color: var(--color-primary);
  position: relative;
  z-index: 5;
  transition: 0.5s;
}

header .background {
  width: 100%;
  height: 250px;
}
@media screen and (min-width: 600px) {
  header .background {
    height: 320px;
  }
}
header img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.container {
  position: absolute;
  width: min(90%, 610px);
  left: 50%;
  transform: translateX(-50%);
  top: 2.5rem;
}
@media screen and (min-width: 600px) {
  .container {
    top: 90px;
  }
}
.container__heading {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 2.5rem;
}

#form__todo {
  background-color: var(--color-primary);
  display: block;
  padding: 1.15rem 1.125rem;
  border-radius: 5px;
  margin-bottom: 2rem;
  display: flex;
  align-items: center;
  gap: 1rem;
}
#form__todo input {
  flex: 1;
  border: 0;
  padding: 0.5rem 0;
  font-family: inherit;
  color: var(--color-secondary-text);
  background-color: var(--color-primary);
  font-size: 1.125rem;
  caret-color: var(--color-active);
}

.todo__list,
.todo__list--options {
  background-color: var(--color-primary);
  overflow: hidden;
}

.todo__list {
  border-radius: 5px 5px 0 0;
}

.todo__list--options {
  border-radius: 5px;
  box-shadow: -5px 8px 30px 8px rgba(0, 0, 0, 0.08);
  justify-content: space-between;
}

.todo__list--options,
.todo__list--sort {
  color: var(--color-light-text);
  display: flex;
  align-items: center;
  padding: 1.25rem 1.5rem;
}
@media screen and (min-width: 768px) {
  .todo__list--options,
  .todo__list--sort {
    padding: 0.8rem 1.5rem;
  }
}
.todo__list--options p,
.todo__list--sort p {
  cursor: pointer;
  transition: color 0.3s;
}
.todo__list--options p:hover,
.todo__list--sort p:hover {
  color: var(--color-primary-text);
}

.todo__list--sort {
  justify-content: center;
  margin-top: 1rem;
  background-color: var(--color-primary);
  border-radius: 5px;
  gap: 1.25rem;
  box-shadow: -5px 8px 30px 8px rgba(0, 0, 0, 0.08);
}
.todo__list--sort.desktop {
  display: none;
}
.todo__list--sort p.active {
  color: var(--color-active);
}
@media screen and (min-width: 768px) {
  .todo__list--sort.mobile {
    display: none;
  }
  .todo__list--sort.desktop {
    display: block;
    display: flex;
    margin-top: 0;
    gap: 1.25rem;
    box-shadow: none;
  }
}

.todo__drag-drop--text {
  text-align: center;
  color: var(--color-light-text);
  margin: 3rem auto;
}

@keyframes blur {
  0% {
    filter: blur(4px);
  }
  100% {
    filter: blur(0);
  }
}